<template>
  <div>
    <h2>Champions League Results</h2>
      <button @click="downloadJson">Download Games JSON</button>
    <ul>
      <li v-for="game in games" :key="game.fixture_id">
        {{ game.teams.away.name }} {{ game.goals.away }} - {{ game.teams.home.name }} {{ game.goals.home }}
      </li>
    </ul>
  </div>
  
</template>

<script>
export default {
  data() {
    return {
        games: [],
        json:[]
    }
  },
  mounted() {
// this.jsonFunc()
// this.fixtures()
// this.fixturesPrem()
    },

    methods: {



  downloadJson() {
      const jsonString = JSON.stringify(this.games);
      const blob = new Blob([jsonString], { type: "application/json" });
      const url = URL.createObjectURL(blob);
      const link = document.createElement("a");
      link.href = url;
      link.download = "games.json";
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },

        fixtures() {
          console.log('test')
                fetch("https://api-football-v1.p.rapidapi.com/v3/fixtures?league=2&season=2022", {
                
      "method": "GET",
      "headers": {
        "x-rapidapi-key": "a93678359bmshec209499c207ebep1758d1jsn22a0484dd70e",
        "x-rapidapi-host": "api-football-v1.p.rapidapi.com"
      }
    })
    .then(response => response.json())
    .then(data => {
        this.games = data.response;
      console.log(this.games)
    })
    .catch(error => {
      console.log(error);
    });

        },
        fixturesPrem() {
          console.log('test')
                fetch("https://api-football-v1.p.rapidapi.com/v3/fixtures?league=39&season=2022", {
                
      "method": "GET",
      "headers": {
        "x-rapidapi-key": "a93678359bmshec209499c207ebep1758d1jsn22a0484dd70e",
        "x-rapidapi-host": "api-football-v1.p.rapidapi.com"
      }
    })
    .then(response => response.json())
    .then(data => {
        this.games = data.response;
      console.log(this.games)
    })
    .catch(error => {
      console.log(error);
    });

        },

        jsonFunc() {
                       
              fetch("../games.json")
  .then((response) => {
                return response.json();
              })
    .then(data => {
        console.log(data)
        this.json = data;
      console.log(this.json)
    })
    .catch(error => {
      console.log(error);
    });
        }
    }
}
</script>
