<template>
  <v-main class="page-color">
    <div class="carousel-wrapper">
      <!-- <v-btn @click="test">test</v-btn> -->
      <v-carousel
        @change="getThisUserGusses(games[$event].fixture.id)"
        class="carousel-wrapper"
        hide-delimiters
        hide-delimiter-background
        show-arrows
      >
        <v-carousel-item
          v-for="(slide, key) in gamesToShow"
          :key="key"
          class=""
        >
          {{ key }}
          <v-row class="mt-7 d-flex justify-space-between title teams-color">
            <v-col cols="2" class="text-end">
              <v-avatar>
                <img :src="slide.teams.home.logo" />
              </v-avatar>
            </v-col>
            <v-col cols="3" class="text-end">
              {{ slide.teams.home.name }}
            </v-col>

            <v-col cols="2" class="text-center">
              {{ slide.goals.away }}:{{ slide.goals.home }}
            </v-col>
            <v-col cols="3" class="text-start">
              {{ slide.teams.away.name }}
            </v-col>
            <v-col cols="2" class="text-start">
              <v-avatar>
                <img :src="slide.teams.away.logo" />
              </v-avatar>
            </v-col>
          </v-row>
          <v-row class="primary mt-10">
            <v-layout child-flex>
              <v-simple-table
                fixed-header
                class="mx-auto text-center"
                calculate-widths
              >
                <template v-slot:default>
                  <thead>
                    <tr class=" ">
                      <th class="text-center">דירוג</th>
                      <th class="text-center">שם</th>
                      <v-spacer></v-spacer>
                      <th class="text-center">נקודות</th>
                      <th class="text-center">ניחוש</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr
                      v-for="(user, i) in dataGuess"
                      :key="i"
                      class="font-weight-bold title"
                    >
                      <td class="">{{ i + 1 }}</td>
                      <td class="">{{ user.userName }}</td>

                      <v-spacer></v-spacer>
                      <td class="">{{ user.sum_points }}</td>
                      <td class="t">
                        {{ user.away_team_goals }}:{{ user.home_team_goals }}
                      </td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </v-layout>
          </v-row>
        </v-carousel-item>
      </v-carousel>
    </div>
  </v-main>
</template>

<script>
import { mapState } from "vuex";
import tables from "../components/table";
export default {
  name: "carousel",
  data() {
    return {
      dataGuess: [],
      index: null,
      currentTitle: null,
      diffBetweenArrays: null,
    };
  },

  components: {
    tables,
  },

  computed: {
    resultarr() {
      return this.$store.state.resultarr;
    },
    users() {
      return this.$store.state.users;
    },
    clientInfo() {
      return this.$store.state.clientInfo;
    },
    items() {
      return this.$store.state.items;
    },

    gamesToShow() {
      let currentTime = new Date().getTime();

      // let finishedGames = this.games.filter((item) => {
      //   const itemTime = Date.parse(item.fixture.date);
      //   return itemTime < currentTime;
      //   // return itemTime < (currentTime - (2 * 60 * 60 * 1000));
      // });
      let games2 = this.games;

      for (let x = 0; x < games2.length; x++) {
        if (Date.parse(games2[x].fixture.date) > currentTime) {
          console.log("small");

          games2.splice(x, 1);
          // console.log(games2);
        }
      }

      return games2.sort((p1, p2) =>
        p1.fixture.id > p2.fixture.id
          ? -1
          : p1.fixture.id < p2.fixture.id
          ? 1
          : 0
      );
    },
    localHost() {
      return this.$store.state.localHost;
    },

    ...mapState(["games"]),
  },

  mounted() {
    this.$store.dispatch("fetchGames");
    console.log(new Date());
  },

  methods: {
    test() {
      console.log(this.currentTitle);
    },

    getThisUserGusses(index) {
      console.log(index);

      fetch(`${process.env.VUE_APP_HOST}guesses/game/${index}`)
        .then((response) => response.json())
        .then((jsonObject) => {
          this.dataGuess = jsonObject;

          console.log(this.dataGuess);
        });
    },
  },
};
</script>

<style>
.carousel-wrapper {
  max-width: 800px;
  margin: 0 auto;
}
.page-color {
  /* background-color: #26000d; */
}
.teams-color {
  /* background-color:#5e0b28 ; */
}
</style>
