<template>
<v-app>
  <toolbar ></toolbar>
    <v-main>
      <router-view/>
    </v-main>
  </v-app>
</template>

<script>
import toolbar from "./components/toolbar";
import Tables from "./views/tableView";
import  Match  from "./views/matchesView";
export default {
  name: 'App',
    components: {
      toolbar,
      Tables,
    Match
  },

  data: () => ({
    //
  }),

    beforeMount() {
    this.checkLocal();
    this.checkLocalb();

  },

  computed: {
       clientInfo() {
      return this.$store.state.clientInfo;
    },
      items() {
      return this.$store.state.items;
    },
  },

  methods: {
       checkLocal() {
      if (localStorage["user"]) {
        this.$store.commit("increment", JSON.parse(localStorage.user));
        console.log(this.clientInfo);
      }
    },
      checkLocalb() {
      if (localStorage["test"]) {
        this.$store.commit("setItems", JSON.parse(localStorage.test));
        // console.log(this.items);
      }
    },
  },
};
</script>
